body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #323b44;
  color: #98a6ad;
}

input[type="checkbox"] {
  -webkit-appearance: initial;
  -moz-appearance: initial;
       appearance: initial;
  background: gray;
  width: 25px;
  height: 25px;
  border: none;
  position: relative;
  margin-bottom: 10px;
  margin-right: 5px;
}

.big-box {
  width: 50px !important;
  height: 50px !important;
}

input[type="checkbox"]:checked {
  background: green;
  margin-right: 0;
}

input[type="checkbox"]:checked:last-child {
  margin-left: 0;
}

input[type="checkbox"]:checked:after {
  /* Heres your symbol replacement - this is a tick in Unicode. */
  content: "\2713";
  color: #fff;
   /* If you want to fully change the check appearance, use the following: */
   content: "";
   width: 100%;
   height: 100%;
   background: blue;
   top: 0;
   left: 0;
}

.card-box {
  padding: 20px;
  border: 1px solid 1px solid rgba(54, 64, 74, 0.08);
  border-radius: 5px;
  background-color: #36404a;
  margin-bottom: 20px;
}

.header-title {
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 700;
}

.header-title-big {
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: 700;
}

#login {
  height: 500px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.fail {
  color: red;
  text-transform: uppercase;
}

.success {
  color: greenyellow;
  text-transform: uppercase;
}

.habitItems {
  padding-right: 0;
  padding-left: 0;
}

input:nth-child(5n) {
  margin-right: 20px;
}

